.glass-card {
    background: linear-gradient(135deg, hsl(var(--glass-bg) / 0.7), hsl(var(--glass-bg) / 0.4));
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    border: 1px solid hsl(var(--glass-border) / 0.3);
    box-shadow: 0 8px 32px 0 hsl(var(--primary) / 0.05);
    /* No black shadow */
    border-radius: 1rem;
  }
.glass-card h1,
  .glass-card h2,
  .glass-card h3,
  .glass-card h4,
  .glass-card h5,
  .glass-card h6 {
    color: hsl(var(--accent));
  }
.glass-card p,
  .glass-card li,
  .glass-card span,
  .glass-card a,
  .glass-card .text-muted-foreground {
    color: hsl(var(--foreground));
  }
.glass-card:hover {
    background: hsl(var(--glass-bg) / 0.9);
    /* Theme color instead of black */
    border: 1px solid hsl(var(--primary) / 0.2);
    box-shadow: 0 10px 40px 0 hsl(var(--primary) / 0.1), inset 0 0 20px hsl(var(--primary) / 0.05);
  }
.glow-text {
    text-shadow: 0 0 40px hsl(var(--primary) / 0.5),
      0 0 80px hsl(var(--primary) / 0.3),
      0 0 120px hsl(var(--accent) / 0.2);
  }
.nav-icon-button.\!active {
    background: linear-gradient(to right,
        hsl(var(--primary) / 0.5) 0%,
        hsl(var(--primary) / 0.3) 20%,
        hsl(var(--primary) / 0.1) 40%,
        hsl(0 0% 0%) 70%,
        hsl(0 0% 0%) 100%) !important;
    box-shadow:
      inset -4px 0 12px hsl(var(--primary) / 0.4),
      0 0 15px hsl(var(--primary) / 0.2),
      0 0 0 1px rgba(0, 0, 0, 0.5) !important;
  }
.nav-icon-button.active {
    background: linear-gradient(to right,
        hsl(var(--primary) / 0.5) 0%,
